


































import Vue from 'vue'
import Component from 'vue-class-component'

// We declare the props separately
// to make props types inferable.
// https://github.com/vuejs/vue-class-component/blob/master/example/src/App.vue#L29-L35
const UserInputProps = Vue.extend({
  props: {
    size: Number,
    min: Number,
    max: Number,
    duration: Number,
    title: String
  }
})
@Component
export default class UserInput extends UserInputProps {
input:any = {
  size: this.size,
  min: this.min,
  max: this.max,
  duration: this.duration
}

activateTimer () {
  console.log('Emmiting activate-timer event')
  this.$emit(
    'activate-timer',
    this.input.size,
    this.input.min,
    this.input.max,
    this.input.duration
  )
}
}
